var width;
export default {
	onLoad: function() {
		uni.getSystemInfo({
			success(res) {
				width = res.screenWidth - 10;
			}
		})
	},
	data() {
		return {

		}
	},
	onReady: function() {
		//this.hideLoading();
	},
	methods: {	
		goDetail:function(id,storeName){			
			let detail = {
					id: id,
					storeName:storeName
				}
				uni.navigateTo({
					url: "dayitem02?detail=" + encodeURIComponent(JSON.stringify(detail))
				})
		}
	}
}
